<template>
  <div class="item-container">
    <a-row type="flex" justify="space-between">
      <a-col :span="4">
        <a target="_blank" :href="houseItem.url">
          <img class="item-img" v-lazy="houseItem.image" alt />
        </a>
      </a-col>
      <a-col class="center-col" :offset="0.5" :span="15">
        <a-row class="center-row title">
          <a-col :span="24">
            <a
              target="_blank"
              :href="houseItem.url"
            >{{houseItem.city}}-{{houseItem.region}}-{{houseItem.position}}</a>
          </a-col>
        </a-row>
        <a-divider></a-divider>
        <a-row class="center-row title sub-title">
          <a-col :span="24">
            <span>{{houseItem.area}}m&sup2;</span>
            <span>/{{houseItem.house_type}}/</span>
            <span>{{houseItem.lease_method}}</span>
          </a-col>
        </a-row>
        <a-divider></a-divider>
        <a-row class="center-row">
          <a-col :span="24">
            <a-tag color="cyan" class="tag" v-for="(item, index) in tags" :key="index">{{item}}</a-tag>
          </a-col>
        </a-row>
      </a-col>
      <a-col class="price" :span="4" :offset="0.5">
        <div>￥{{houseItem.rent}}元/月</div>
        <a-popconfirm placement="top" ok-text="是" cancel-text="否" @confirm="remove">
          <template slot="title">
            <p>确认删除？</p>
          </template>
          <a-button class="btn" type="primary">删除数据</a-button>
        </a-popconfirm>
      </a-col>
    </a-row>
  </div>
</template>

<script>
import { removeHouse } from "network/houseList";

export default {
  props: {
    houseItem: {
      type: Object,
      default() {
        return {};
      }
    },
    pk: Number
  },
  computed: {
    tags() {
      return this.houseItem.tags.split(";");
    }
  },
  methods: {
    remove() {
      removeHouse(this.pk)
        .then(res => {
          if (res.data.flag && res.data.data.row > 0) {
            this.$message.success("删除成功！");
            this.$emit("removeHouse");
          } else {
            this.$message.error("删除失败！" + res.data.error);
          }
        })
        .catch(err => {
          this.$message.error("删除失败！" + err);
        });
    }
  }
};
</script>

<style scoped>
.center-col {
  padding-left: 30px;
  /* background-color: #00a0e9; */
}
.item-container {
  margin-top: 15px;
  /* background-color: rgba(238, 230, 195, 0.3); */
  padding: 10px;
  border-radius: 10px;
  border-top: #73c3e9 solid 3px;
}
.item-img {
  border-radius: 10px;
  width: 250px;
  height: 182px;
}
.title {
  font-size: 20px;
  font-weight: 700;
  margin-top: 0;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.title a {
  color: #666666 !important;
}
.title a:hover {
  color: #00a0e9 !important;
}
.sub-title {
  font-size: 15px;
  color: #999999;
}
.sub-title span:not(:first-child) {
  margin-left: 10px;
}
.tag {
  color: #aaaaaa;
}
.price {
  color: #f14029;
  font-size: 25px;
  font-weight: 800;
  padding-top: 50px;
  text-align: center;
}
.btn {
  margin-top: 20px;
}
</style>