import {request} from "./request"

// 获取租房列表数据
export function findHouseList(queryVo) {
    return request({
        url: "/findHouseList",
        params: {
            currentPage: queryVo.currentPage,
            pageSize: queryVo.pageSize,
            orderBy: queryVo.orderBy,
            condition: queryVo.condition
        }
    })
}

// 删除指定的某一条数据
export function removeHouse(pk) {
    return request({
        url: "/removeHouse",
        params: {
            pk
        }
    })
}