<template>
  <div id="house-list" ref="houseList">
    <a-affix :target="() => this.$refs.houseList">
      <house-header
        @changeSort="changeSort"
        @changeCondition="changeCondition"
        @backTop="backTop"
        class="house-header"
      ></house-header>
    </a-affix>
    <a-empty v-show="houseList.length === 0" description="暂无数据" class="empty" />
    <house-item
      v-for="(item, index) in houseList"
      :key="index"
      :pk="item.pk"
      :house-item="item.fields"
      @removeHouse="removeHouse"
    ></house-item>
    <a-pagination
      class="pagination"
      :current="currentPage"
      :page-size="pageSize"
      :total="total"
      @change="onChange"
    />
  </div>
</template>

<script>
import HouseHeader from "./childComp/HouseHeader";
import HouseItem from "./childComp/HouseItem";

import { findHouseList } from "network/houseList";

export default {
  name: "HouseList",
  components: {
    HouseHeader,
    HouseItem
  },
  data() {
    return {
      currentPage: 1,
      pageSize: 10,
      condition: "",
      orderBy: "rent",
      total: 0,
      houseList: []
    };
  },
  mounted() {
    this.getHouses();
  },
  methods: {
    // 界面事件控制
    backTop() {
      document.querySelector("#house-list").scrollTo({
        top: 0,
        left: 0,
        behavior: "smooth"
      });
    },
    onChange(currentPage) {
      this.currentPage = currentPage;
      this.getHouses();
      this.backTop();
    },
    changeCondition(condition) {
      this.condition = condition;
      this.currentPage = 1;
      this.getHouses();
    },
    changeSort(orderBy) {
      this.orderBy = orderBy;
      this.currentPage = 1;
      this.getHouses();
    },
    // ajax请求
    getHouses() {
      const queryVo = {
        currentPage: this.currentPage,
        pageSize: this.pageSize,
        orderBy: this.orderBy,
        condition: this.condition
      };
      findHouseList(queryVo).then(res => {
        this.houseList = res.data.data.houseList;
        this.total = res.data.data.total;
      });
    },
    removeHouse() {
      this.getHouses();
    }
  }
};
</script>
<style scoped>
#house-list {
  height: 600px;
  overflow: hidden;
  overflow-y: scroll;
}
.house-header {
  position: sticky;
}
.pagination {
  float: right;
  position: fixed;
  bottom: 10px;
  right: 30px;
}
.empty {
  margin-top: 30px;
}
</style>