<template>
  <div>
    <a-menu v-model="current" mode="horizontal" @click="backTop">
      <a-menu-item key="price" @click="changeSort('price')">
        <a-icon type="pay-circle" />价格排序
        <a-icon v-show="priceSort.show" v-if="priceSort.asc" type="rise" />
        <a-icon v-show="priceSort.show" v-else type="fall" />
      </a-menu-item>
      <a-menu-item key="app" @click="changeSort('area')">
        <a-icon type="home" />面积排序
        <a-icon v-show="areaSort.show" v-if="areaSort.asc" type="rise" />
        <a-icon v-show="areaSort.show" v-else type="fall" />
      </a-menu-item>
      <a-sub-menu>
        <span slot="title" class="submenu-title-wrapper">
          <a-icon type="down-square" />城市筛选
        </span>
        <a-menu-item
          @click="changeCondition(item)"
          v-for="(item, index) in city"
          :key="index"
        >{{item}}</a-menu-item>
      </a-sub-menu>
    </a-menu>
  </div>
</template>
<script>
export default {
  name: "HouseHeader",
  data() {
    return {
      current: ["price"],
      city: ["全选", "北京", "上海", "深圳", "广州"],
      condition: "",
      orderBy: "",
      priceSort: {
        show: true,
        asc: true
      },
      areaSort: {
        show: false,
        asc: false
      }
    };
  },
  methods: {
    // 改变查询条件相关
    changeSort(key) {
      switch (key) {
        case "price":
          this.priceSort.show = true;
          this.areaSort.show = false;
          this.areaSort.asc = false;
          this.priceSort.asc = !this.priceSort.asc;
          break;
        case "area":
          this.areaSort.show = true;
          this.priceSort.show = false;
          this.priceSort.asc = false;
          this.areaSort.asc = !this.areaSort.asc;
          break;
      }
      if (this.priceSort.show) {
        this.orderBy = this.priceSort.asc ? "rent" : "-rent";
      } else if (this.areaSort.show) {
        this.orderBy = this.areaSort.asc ? "area" : "-area";
      }
      this.$emit("changeSort", this.orderBy);
    },
    changeCondition(condition) {
      if (condition === "全选") {
        condition = ""
      }
      this.condition = condition;
      this.$emit("changeCondition", this.condition);
    },
    backTop() {
      this.$emit("backTop");
    }
  }
};
</script>

